import { useMemo } from 'react';
import { AxisValueRange } from '../types.js';
import { getYAxisOptionsWithDefaults } from '../../../utilities/getAxisOptions.js';
import { getStackedMinMax } from '../../../utilities/getStackedMinMax.js';
import { yAxisMinMax } from '../../LineChart/utilities/yAxisMinMax.js';

function useGetDualAxis({
  data
}) {
  return useMemo(() => {
    return data.map((series, index) => {
      let min;
      let max;
      const yAxisOptions = getYAxisOptionsWithDefaults(series.yAxisOptions);

      switch (series.shape) {
        case 'Line':
          {
            const {
              minY,
              maxY
            } = yAxisMinMax(series.series);
            min = minY;
            max = maxY;
            break;
          }

        case 'Bar':
          {
            const {
              min: yMin,
              max: yMax
            } = getStackedMinMax({
              stackedValues: null,
              data: series.series,
              integersOnly: yAxisOptions.integersOnly
            });
            min = yMin;
            max = yMax;
            break;
          }
      }

      return {
        valuesRange: getValuesRange(min, max),
        min,
        max,
        index,
        shape: series.shape,
        yAxisOptions,
        name: series.name
      };
    });
  }, [data]);
}

function getValuesRange(min, max) {
  if (min <= 0 && max <= 0) {
    return AxisValueRange.AllNegative;
  } else if (min < 0 || max < 0) {
    return AxisValueRange.SomeNegative;
  } else {
    return AxisValueRange.AllPositive;
  }
}

export { useGetDualAxis };
