function formatDataForTooltip({
  data,
  xAxisOptions
}) {
  const formattedData = data.data.map(data => {
    return { ...data,
      data: data.data.map(values => {
        return { ...values,
          key: `${values.key}`,
          value: `${values.value}`
        };
      })
    };
  });
  return {
    formattedData,
    title: data.title ? xAxisOptions.labelFormatter(data.title) : undefined
  };
}

export { formatDataForTooltip };
