import { getGapForIndex } from '../../../utilities/getGapForIndex.js';

function getYPosition({
  start,
  end,
  groupIndex,
  gaps,
  yScale
}) {
  if (start < 0) {
    return yScale(end) + getGapForIndex({
      gaps,
      direction: 'negative',
      seriesIndex: groupIndex
    });
  }

  if (end > 0) {
    return yScale(end) - getGapForIndex({
      gaps,
      direction: 'positive',
      seriesIndex: groupIndex
    });
  }

  return 0;
}

export { getYPosition };
