function getSeriesColors(count, selectedTheme) {
  if (count === 1) {
    return [selectedTheme.seriesColors.single];
  }

  if (count <= selectedTheme.seriesColors.limited.length) {
    return [...selectedTheme.seriesColors.limited];
  }

  const allColors = [...selectedTheme.seriesColors.all];

  if (count <= allColors.length) {
    return allColors;
  }

  let lastUsedColorIndex = -1;
  return [...Array.from({
    length: count
  })].map(() => {
    lastUsedColorIndex += 1; // Once we've hit the last item in the array,
    // reset the count and grab the first color.

    if (lastUsedColorIndex === allColors.length) {
      lastUsedColorIndex = 0;
    }

    return allColors[lastUsedColorIndex];
  });
}

export { getSeriesColors };
